.card {
  display: flex;
  flex-direction: column;
  width: 25%;
  padding: 1em;
  box-shadow: 0 2px 8px rgba(255, 255, 255, 0.658);
  border-radius: 16px;
  background: linear-gradient(to right bottom, #B2F9FC, #D5D5D5);
  backdrop-filter: blur(0.5rem);
  align-self: center;
  align-items: center;
  margin: 0 2vh 2vh;
  z-index: 2;
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
    margin: 1rem;
  }
}