.photoBox,
.dateBox,
.authorBox {
    margin-top: 0.5em;
}

.photoBox {
    max-width: 100%;
    border-radius: 8px;
    transition: ease-in-out 0.1s;
}

.photoBox:hover {
    transform: scale(1.025);
    cursor: pointer;
}

.dateBox,
.authorBox {
    justify-content: flex-start;
    margin-right: auto;
}


.interactionSection {
    display: flex;
    flex-direction: row;
    margin-top: 0.5em;
    width: 100%;
    height: auto;
    justify-content: space-between;
    align-content: center;
}

.learnMoreButton {
    padding: 0.2em 0.4em 0.2em 0.4em;
    cursor: pointer;
    overflow: hidden;
    outline: none;
    color: #000000;
    background-color: transparent;
    border-radius: 0.5em;
    border: 0.15em solid #000000;
    transition: all .1s ease-in-out;
}

.learnMoreButton:hover {
    background-color: #000000;
    color: #D5D5D5;
    transition: all .2s ease-in-out;
}

.learnMoreButton:active {
    transform: scale(0.95);
    transition: all .3s ease-in-out;
}

.statusIcon {
    cursor: pointer;
    width: 1.5em;
    height: 1.5em;
    margin-left: 0.25em;
}

.statusIcon:active {
    animation: changingLikeStatus 0.1s ease-in-out;
}

@keyframes changingLikeStatus {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(0.9);
    }

    100% {
        transform: scale(1.5);
        /* Creates an out of bounds clicking sensation*/
    }
}
